body {
  @include oswald($regular);
  font-size: 14px;
  line-height: 18px;
  color: $black;
  margin: 0;
  counter-reset: num;
  overflow-x: hidden;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
